.Units {
  min-height: 60px;
  min-width: 60px;
  border: 1px solid white;
  background: transparent;
}
.Unit {
  max-height: 60px;
  max-width: 60px;
  background: transparent;
}
.Unit.attacked {
  border: 1px solid red
}
.Unit.attacker {
  border: 1px solid blue;
}
.unit-health {
  position: absolute;
}




.line {
  position: absolute
}


